
import React, { Component } from 'react';
import { connect } from "react-redux";

import { Link } from "react-router-dom";
import { CardBody, CardText, Button } from "reactstrap";

//Import Action to copy breadcrumb items from local state to redux state
import { setBreadcrumbItems } from "../../../store/actions";


class Notifications extends Component {
    constructor(props) {
        super(props);
        this.state = {
            breadcrumbItems : [
                { title : "Buytale", link : "/" },
                { title : "Integrations", link : "#" },
                { title : "Notifications", link : "/notifications" },
            ],
        }
    } 

    componentDidMount(){
        this.props.setBreadcrumbItems("Notifications", this.state.breadcrumbItems);
    }

    render() {
        return (
            <React.Fragment>
                        <div className="card card-body text-center">
                            <CardBody>
                                <h1 className="display-1"><i className="fa fa-plug"></i></h1>
                                <CardText>Get notifications by connecting with third party applications such as Slack to alert you when certain events occur.
                                </CardText>
                                <Button tag={Link} to="/billing/plans" color="success" size="lg"  className="waves-effect waves-light">Setup notification</Button>
                            </CardBody>
                        </div>
            </React.Fragment>
        );
    }
}

export default connect(null, { setBreadcrumbItems })(Notifications)
