import React, { Component } from 'react';
import { connect } from "react-redux";


import { Link } from "react-router-dom";
import { CardBody, CardText, Button } from "reactstrap";

//Import Action to copy breadcrumb items from local state to redux state
import { setBreadcrumbItems } from "../../../store/actions";


class Variables extends Component {
    constructor(props) {
        super(props);
        this.state = {
            breadcrumbItems : [
                { title : "Buytale", link : "/" },
                { title : "Webstore", link : "#" },
                { title : "Variables", link : "/design/currency" },
            ],
        }
    } 

    componentDidMount(){
        this.props.setBreadcrumbItems("Variables", this.state.breadcrumbItems);
    }

    render() {
        return (
            <React.Fragment>
                        <div className="card card-body text-center">
                            <CardBody>
                                <h1 className="display-1"><i className="fa fa-th-list"></i></h1>
                                <CardText>Variables enable you to create customisable packages such as allowing users to select the colour of their name upon purchase.
                                </CardText>
                                <Button tag={Link} to="/billing/plans" color="success" size="lg"  className="waves-effect waves-light">Upgrade to the Premium Plan</Button>
                            </CardBody>
                        </div>                               
            </React.Fragment>
        );
    }
}

export default connect(null, { setBreadcrumbItems })(Variables)