import React, { Component } from 'react';
import { connect } from "react-redux";

//Import Action to copy breadcrumb items from local state to redux state
import { setBreadcrumbItems } from "../../../../store/actions";


class BasketCoupons extends Component {
    constructor(props) {
        super(props);
        this.state = {
            breadcrumbItems : [
                { title : "Buytale", link : "/" },
                { title : "Webstore", link : "#" },
                { title : "Basket Settings", link : "#" },
                { title : "Maximum Coupons Per Basket", link : "/basketsettings/coupons" },
            ],
        }
    } 

    componentDidMount(){
        this.props.setBreadcrumbItems("Maximum Coupons Per Basket", this.state.breadcrumbItems);
    }

    render() {
        return (
            <React.Fragment>
                              
            </React.Fragment>
        );
    }
}

export default connect(null, { setBreadcrumbItems })(BasketCoupons)